#CharacterPic {
  /* width: 50vw;
  max-height: 76vh; */
  object-fit: cover;
  display: block;
  margin-left: auto;
  margin-right: auto;
  width: 70%;
  max-height: 70%;
}

html {
  height: 100%;
}

body {
  /* The image used */
  background-image: url("/src/img/background/inazuma_backgroun_mobile.png");
  /* Center and scale the image nicely */
  background-position: center;
  background-repeat: no-repeat;
  background-size: 100% 100%;
}

.FrontPage {
  text-align: center;
}

.homebutton {
  margin: 0;
  position: absolute;
  top: 50%;
  left: 50%;
  -ms-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
}
@media only screen and (min-width: 768px) {
  /* For desktop: */

  body {
    /* The image used */
    background-image: url("/src/img/background/inazuma_backgroun.png");
    /* Center and scale the image nicely */
    background-position: center;
    background-repeat: no-repeat;
    background-size: 100% 100%;
  }

  #CharacterPic {
    width: 37vw;
    max-height: 80vh;
    object-fit: cover;
    display: block;
    margin-left: auto;
    margin-right: auto;
  }

  html {
    height: 100%;
  }
  .FrontPage {
    text-align: center;
  }

  .homebutton {
    margin: 0;
    position: absolute;
    top: 50%;
    left: 50%;
    -ms-transform: translate(-50%, -50%);
    transform: translate(-50%, -50%);
  }
}
